import {
  Autocomplete,
  Box,
  Dialog,
  DialogContent,
  Grow,
  LinearProgress,
  TextField,
} from "@mui/material";
import { useHotkeys } from "react-hotkeys-hook";
import { useState } from "react";
import { useQuery } from "@tanstack/react-query";
import axios from "axios";

const QuickFind = () => {
  const [open, setOpen] = useState(false);
  useHotkeys("ctrl+k", () => setOpen(true), []);
  const { data = [], isLoading } = useQuery({
    queryKey: ["countries"],
    queryFn: async () => {
      return axios
        .get<Country[]>("https://restcountries.com/v3.1/all")
        .then(({ data }) => data);
    },
    enabled: open,
  });

  return (
    <Dialog
      open={open}
      onClose={() => {
        setOpen(false);
      }}
      slotProps={{
        backdrop: {
          sx: { display: "none" },
        },
      }}
      TransitionComponent={Grow}
    >
      <DialogContent>
        <Autocomplete
          id="combo-box-demo"
          options={data}
          sx={{ width: 500 }}
          loading={isLoading}
          autoHighlight
          getOptionLabel={(option) => option.name.common}
          renderOption={(props, option) => {
            return (
              <Box
                key={option.name.common}
                component="li"
                sx={{ "& > img": { mr: 2, flexShrink: 0 } }}
                {...props}
              >
                <img
                  loading="lazy"
                  width="20"
                  src={option.flags.png}
                  alt={option.flags.alt}
                />
                {option.name?.common}
              </Box>
            );
          }}
          renderInput={(params) => <TextField {...params} label="Movie" />}
        />
      </DialogContent>
      {isLoading && <LinearProgress />}
    </Dialog>
  );
};

interface Country {
  name: {
    common: string;
  };
  flags: {
    png: string;
    alt: string;
  };
}
export default QuickFind;
